            <v-col>
              <v-select
                :item-props="itemProps"
                v-model="selectedMainGroup"
                :items="mainGroups"
                :label="$t('mainGroup')"
                dense
                solo
                outlined
                hide-details
                item-text="Name"
                item-value="MainGroupID"
